:root {
    --white-color: #fff;
    --yellow-color: #eac645;
    --violet-color: #4f2ee8;
    --black-first: #202024;
    --black-second: #111;
  }
  
  body {
    box-sizing: border-box;
    color: #fff;
    background: linear-gradient(to right, #000000e0 53%, #57b8ff 50%);
    height: 100vh;
    margin: 0;
    padding: 0;
    font-family: Orbitron, sans-serif;
  }
  
  h1, h2, h3, h4, h5, h6 {
    margin: 0;
  }
  
  a {
    color: inherit;
    text-decoration: none;
  }
  
  h1, h2, h3, h4, h5, h6, p {
    margin: 0;
  }
  
  p:last-child {
    margin-bottom: 0;
  }
  
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
  button {
    all: unset;
  }

  * {
    box-sizing: border-box;
  }